import { Dispatch } from "react";
import { SlideName } from "./slides";

export interface SlidesProviderState {
  currentSlideName: SlideName;
  formData: FormData;
}

export enum ActionTypes {
  ChangeSlide,
  MergeFormData,
}

type SlidesReducerDispatch = Dispatch<{
  type: ActionTypes;
  payload?: Partial<SlidesProviderState>;
}>;

export const changeSlide = (dispatch: SlidesReducerDispatch) => (
  slideName: SlideName
) =>
  dispatch({
    type: ActionTypes.ChangeSlide,
    payload: { currentSlideName: slideName },
  });

export const mergeFormData = (dispatch: SlidesReducerDispatch) => (
  formData: FormData
) =>
  dispatch({
    type: ActionTypes.ChangeSlide,
    payload: { formData },
  });

export const reducer = (
  state: SlidesProviderState,
  action: { type: ActionTypes; payload?: Partial<SlidesProviderState> }
): SlidesProviderState => {
  switch (action.type) {
    case ActionTypes.ChangeSlide:
      return {
        ...state,
        currentSlideName: action.payload?.currentSlideName as SlideName,
      };
    case ActionTypes.MergeFormData:
      return {
        ...state,
        formData: mergeForms(
          state.formData,
          action.payload?.formData as FormData
        ),
      };
    default:
      throw new Error("Unknown action.type");
  }
};

const mergeForms = (...forms: FormData[]): FormData => {
  const mergedFormData = new FormData();

  forms.forEach((form) =>
    Array.from(form.entries()).forEach(([key, value]) =>
      mergedFormData.append(key, value)
    )
  );

  return mergedFormData;
};
